html, body, div, span {
  margin: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
:root {
  --back-one: #b360d1;
  --back-two: #94abed;
  --toolbar-one: #751CC9;
  --toolbar-two: #2e294e;
  --text: #f4f4f6;
  --divider: #BDBDBD;
  --text-primary: #262730;
}

* {
  box-sizing: border-box;
}
body {
  height: 100vh;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  background: linear-gradient( -90deg, 
    #b360d1,
    #94abed);
  background-repeat: no-repeat;
  background-attachment: fixed;
}

/* Bootstrap resetters */
.row {
  padding-left: 15px;
  padding-right: 15px;
}
.col-md-12 {
  padding: 0px 0px 17px 0px;
}
@media (min-width: 992px) {
  .col-lg-6 {
    padding-left: 15px;
    padding-right: 15px;
  }
  .col-md-12 {
    padding-bottom: 0px;
  }
}
#root {
  padding-left: 0.25rem;
  padding-right: 0.25rem;
}
.header {
  font-family: "Roboto Mono", mono;
  padding-top: 1.1rem;
  padding-bottom: 1.1rem;
  width: 100%;
  text-align: center;
  color: var(--text);
}
  .header .text-center {
    font-size: 1.45rem;
  }
.editor,
.previewer {
  margin-bottom: 30px;
  transition: 0.37s;
  background: var(--text);
  color: var(--primary-text);
  box-shadow: 0px 10px 12px 0px rgba(0,0,0,0.59);
  padding: 3px 12px;
}
  .editor {
    width: 100%;
    height: 300px;
    font-family: "Roboto Mono", mono;
    letter-spacing: 1px;
    resize: none;
    border: 0;
  }
    #editor .bar-toggle {
      background: var(--toolbar-one);
    }
  .previewer {
    width: 100%;
    height: 300px;
    overflow: scroll;
    overflow-x: hidden;
  }
    #previewer .bar-toggle {
      background: var(--toolbar-two);
    }
.bar-toggle {
  font-size: 1.32rem;
  text-transform: uppercase;
  color: var(--text);
}
  .bar-toggle .text-wrapper {
    display: inline-block;
    width: 92%;
  }
    .bar-toggle .text-wrapper h2 {
      padding-left: 1.3rem;
    }
.maximized {
  height: 0 !important;
  padding: 0;
  display: block;
  border: 0;
}

.bar-toggle {
  width: 100%;
  color: var(--text);
  padding-top: 0.53rem;
  padding-bottom: 0.43rem;
  cursor: pointer;
}
  .bar-toggle h2 {
    font-weight: 700;
    font-size: 1.1rem;
    margin: 0;
    padding: 0;
    letter-spacing: 1.1px;
  }

@media (min-width: 992px) {
  .editor,
  .previewer {
    height: 500px;
  }
  .maximized {
    height: 130px !important;
    padding: 5px 12px;
  }
  .bar-toggle .text-wrapper {
    width: 96%;
  }
}

@media (min-width: 1200px) {
  .header .text-center {
    font-size: 1.6rem;
  }
  .previewer {
    height: 450px;
  }
  .editor {
    height: 450px;
  }
}

